import React from "react";

class Settings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div>
                <div style={{ fontSize: "30px" }}>Settings work in progress</div>
            </div>
        );
    }
}

export default Settings;


